.NavBar{
    background-color: white;
    position: fixed;
    width: 100vw;
    max-height: 10vh;
    display: flex;
    flex-direction: row;
    align-items: left;
}
.title{
    margin-top: 1%;
    margin-bottom: 1%;
    font-size:300%;
    align-items: left;
    margin-left: 5%;
}
.Navlinks{
    margin-left: 5%;
    margin-top: 20px;
    margin-bottom: 15px;
    width:70vw;
}
.NavItem{
    margin-top: 1%;
    margin-bottom: 1%;
    color:gray;
    width: 10%;
    text-decoration: none;
    font-size: 36px;
    border-bottom: black;
}
.NavItem.active{
    color: black;
    border-bottom:solid;
}
.NavItem:hover{
    color: black;
    border-bottom: solid;
}
ul{
    margin-left: 10%;
   list-style: none;
   display: flex; 
   flex-direction: row;
}
li{
    width: 10%;
    text-align: center;
    justify-content: center;
}
.NavItem Link{
    text-decoration: none;
    top: 20%;
    margin-bottom: 30%;
}